
// 
export const common={
	baseUrl: "https://information.zzqtrj.net",
	header:{
		'Content-Type':'application/json;charset=UTF-8',
	},
	data:{request:''},
	method:'GET',
	dataType:'json'
}

export default {
	//  promise
	request(options = {}){
		// 
		options.url = common.baseUrl + options.url
		options.header = options.header || common.header
		options.data = options.data || common.data
		options.method = options.method || common.method
		options.dataType = options.dataType || common.dataType
		
		// 
		return new Promise((res,rej)=>{
			// ... todos
			// uni.showLoading({
			// 	title:'...'
			// })
			// ...
			uni.request({
				...options,
				success: (result) => {
					// 
					if(result.statusCode!==200){
						if(result.data.msg==''){
							uni.showToast({
								title: '',
								icon: 'error'
							})						
							// console.log('');
							// uni.removeStorageSync('token')
							// uni.removeStorageSync('avatar')
							// uni.removeStorageSync('name')
							// uni.removeStorageSync('type')
							setTimeout(function() {
								uni.switchTab({
									url:'/pages/my/my'
								})
							}, 1000)
							return
						}
						uni.showToast({
							title:result.data.msg || '',
							icon:'none'
						})
						rej(result.data)
					}
	
					return res(result.data)
				},
				fail: (error) => {
					uni.showToast({
						title: error.errMsg || '',
						icon: 'none'
					});
					return rej()
				},
				complete: () => {
					// uni.hideLoading()
				}
			});
		})
	},
	// post
	post(url,data,options = {}){
		options.url = url
		options.data =data?{ request_data:data }:{request:''}
		options.method = 'POST'
		return this.request(options)
	}
}